<template>
  <div class="pintuanma">
    <!--头部-->
    <header class="aui-bar aui-bar-nav" id="aui-header">
      <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
        <span class="aui-iconfont aui-icon-left"></span>
      </a>
      <div class="aui-title aui-font-size-12">我的拼团码</div>

    </header>
    <!--内容区 不同拼团码信息-->
    <div class="aui-card-list-header aui-card-list-user bg-background">

      <div class="z-shebei-box1 z-pintuan-b">
        <template v-for="item in lists">
          <!--<router-link to="/center/myshebei1/shebeikongzhi1">-->
            <div class="aui-font-size-14 text-light">{{item.name}}</div>
            <div class="aui-card-list-user-name">
              <img :src="'data:image/png;base64,'+item.url" alt="" style="margin: auto;">
            </div>
          <!--</router-link>-->
        </template>

      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    name: "pintuanma",
    data() {
      return {
        userId: '1',
        lists: [],
      }
    },

    created: function () {
      this.myPinTuan();
    },
    methods: {
      myPinTuan: function () {
        //初始化加载的数据
        var data = new FormData();
        data.append('userId', this.userId);
        //发送接口请求
        axios
          .post('http://140.143.200.89:8080/dishwasher/selectToUserDeviceStyle', data)
          .then(response => {
            this.lists = response.data;


          })
      }
    }


  }
</script>

<style scoped>
  .z-pintuan-b{border-bottom: 1px solid #fff!important;}

</style>



