<template>
    <div class="jifentixian">
      <!--头部-->
      <header class="aui-bar aui-bar-nav z-bar-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12">积分提现</div>
      </header>
      <!--内容区-->
      <div class="aui-list-item aui-list-item-middle"
           :style="note">
        <div class="aui-card-list-content aui-text-center z-padding-t3">
          <div class="aui-font-size-12 colorWhite">当前金额(元)</div>
          <p class="aui-margin-b-10 z-font-size28  colorWhite">{{lists.userCurrentScore}}</p>
        </div>
      </div>
      <!--积分金额与已提现-->
      <div class="aui-row" style="background: white;padding: 5px 0px;">
        <div class="aui-col-xs-6 aui-border-r aui-text-center">
          <big class="aui-text-warning"><small>{{lists.userTotalScore}}</small></big>
          <div class="aui-gird-lable aui-font-size-12">积分总额</div>
        </div>
        <div class="aui-col-xs-6 aui-border-r aui-text-center">
          <big class="aui-text-danger"><small> {{lists.userConsumeScore}}</small></big>
          <div class="aui-gird-lable aui-font-size-12">已提现</div>
        </div>
      </div>
      <!--提现账号管理-->
      <ul class="aui-list aui-list-in z-margin-t5 z-background-image">
        <li class="aui-list-item">
          <router-link class="aui-list-item-arrow" to="/center/jifentixian1/addzhanghu">
            <div class="aui-list-item-inner">
              <div class="aui-list-item-title aui-font-size-12">提现账号管理</div>
            </div>
          </router-link>

        </li>
        <li class="aui-list-item">

          <div class="aui-list-item-inner">

              <router-link class="aui-list-item-arrow" to="/center/jifentixian1/tixianjilu1">
                <div class="aui-list-item-title aui-font-size-12">提现记录</div>
              </router-link>

          </div>

        </li>
        <li class="aui-list-item">

          <div class="aui-list-item-inner">

            <router-link class="aui-list-item-arrow" to="/center/jifentixian1/jifenmingxi1">
              <div class="aui-list-item-title aui-font-size-12">积分明细</div>
            </router-link>

          </div>

        </li>
      </ul>
      <router-link to="/center/tixian">
        <div class="aui-btn  aui-btn-block aui-btn-sm  btn-bg colorWhite" style="margin-top: 80px;">提现</div>
      </router-link>

      <router-view></router-view>
    </div>
</template>

<script>
    import axios from 'axios'
    export default {
      name: "jifentixian",
      data(){
        return{
          //添加图片背景
          note: {
            backgroundImage: "url(" + require("../../images/center_bg.png") + ")",
            backgroundRepeat: "no-repeat",
            backgroundSize: "cover",
            height: "160px",
            position: "relative"
          },

          userId:'1',

          lists:{},
        }
      },
      created:function(){
        this.cashSummary();
      },
      methods:{
        // 提现事件
        cashSummary:function () {
          //初始化加载的数据
          var data = new FormData();
          data.append('userId', this.userId);


          axios
            // .post('http://140.143.200.89:8080/dishwasher/selectByCashSummary',data)
            .post('http://xiwanji.91xiaokong.com/selectByCashSummary',data)
            .then(response =>{

              this.lists = response.data
             // console.log(JSON.stringify(this.lists));


            })
        }

      },
    }
</script>

<style scoped>



</style>



