<template>
    <div class="jifenmingxi1">
      <!--头部-->
      <header class="aui-bar aui-bar-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12">积分明细</div>
      </header>

      <!--收支明细流水-->
      <ul class="aui-list aui-media-list">
        <template v-for="items in lists">
          <li class="aui-list-item">
            <div class="aui-media-list-item-inner">
              <div class="aui-list-item-inner">
                <div class="aui-list-item-title aui-font-size-18">
                  <span class="aui-pull-left aui-font-size-12">{{items.scoreItemType}}</span>
                  <span class="aui-pull-right colorOrage aui-font-size-12">￥{{items.scoreItemScore}}</span>
                </div>

              </div>
            </div>
            <div class="aui-info" style="margin-top: -14px;">
              <div class="aui-info-item aui-font-size-12">
                <span class="aui-margin-l-5">时间:{{items.scoreItemDate}}</span>
              </div>

              <div class="aui-info-item aui-font-size-12 aui-pull-right" v-if="items.scoreItemState ==0">收益</div>
              <div class="aui-info-item aui-font-size-12 aui-pull-right" v-if="items.scoreItemState ==1">支出</div>
            </div>

          </li>
        </template>


      </ul>

    </div>
</template>

<script>
  import axios from 'axios'
    export default {
        name: "jifenmingxi1",
        data(){
            return{
              userId:'1',
              lists:[]
            }
        },
      created:function () {
        this.ScoreItem();
      },
      methods:{
        ScoreItem:function () {
          //初始化加载的数据
          var data = new FormData();
          data.append('userId', this.userId);

          //发送接口请求
          axios
            // .post('http://140.143.200.89:8080/dishwasher/selectByScoreItem',data)
            .post('http://xiwanji.91xiaokong.com//selectByScoreItem',data)
            .then(response =>{

              this.lists= response.data;


            })
        }
      }

    }
</script>

<style scoped>

</style>



