<template>
    <div class="tixianjilu">
      <!--aaa-->
      <!--头部-->
      <header class="aui-bar aui-bar-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12">提现记录</div>
      </header>
      <!--记录内容-->
      <ul class="aui-list aui-media-list">
        <template v-for="items in lists.list">
          <li class="aui-list-item">
            <div class="aui-media-list-item-inner">
              <div class="aui-list-item-inner">
                <div class="aui-list-item-title aui-font-size-18">
                  <span class="aui-pull-left aui-font-size-14">微信支付</span>
                  <span class="aui-pull-right aui-font-size-12 colorOrage">￥{{items.cashScore}}</span>
                </div>

              </div>
            </div>
            <div class="aui-info" style="margin-top: -14px;">
              <div class="aui-info-item aui-font-size-12">
                <span class="aui-margin-l-5">账号:{{items.cashAccountId}}</span>

              </div>

              <div class="aui-info-item aui-font-size-12" v-if="items.cashState ==1">审核中</div>
              <div class="aui-info-item aui-font-size-12" v-if="items.cashState ==0">已通过</div>
            </div>
            <p class="aui-margin-l-5 z-padding-b-5 aui-font-size-12">时间:{{items.cashDate}}</p>
          </li>
        </template>


      </ul>

    </div>
</template>

<script>
  import axios from 'axios'

    export default {
        name: "tixianjilu",
        data(){
           return{
             userId:'1',
             pageNum:'2',
             pageSize:'2',

             lists:[],
           }
        },
      created:function(){
        this.cashRecord();
      },
      methods:{
        // 提现事件
        cashRecord:function () {
          //初始化加载的数据
          var data = new FormData();
          data.append('userId', this.userId);
          data.append('pageNum', this.pageNum);
          data.append('pageSize', this.pageSize);

          axios
            // .post('http://140.143.200.89:8080/dishwasher/selectByCashRecord',data)
            .post('http://xiwanji.91xiaokong.com//selectByCashRecord',data)
            .then(response =>{
              this.lists = response.data
            })
        }

      },
    }
</script>

<style scoped>

</style>



