<template>
  <div class="myshebei">
    <!--头部-->
    <header class="aui-bar aui-bar-nav" id="aui-header">
      <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
        <span class="aui-iconfont aui-icon-left"></span>
      </a>
      <div class="aui-title aui-font-size-12">我的设备</div>

    </header>
    <!--内容区 不同设备信息-->
    <div class="aui-card-list-header aui-card-list-user bg-background" style="padding: 0 !important;">
      <template v-for="items in lists.list">
        <div class="z-shebei-box1">


          <!--<router-link to="/center/myshebei1/xufei">-->
          <div class="aui-card-list-user-name">
            <router-link to="/center/myshebei1/shebeikongzhi1">
              <div>{{items.deviceName}}</div>
            </router-link>
            <div class="aui-font-size-14 text-light">
              <span>设备状态</span>

            </div>
          </div>


          <div class="z-shebei-xian">
            <p></p>
          </div>

          <div class="userinfo-header">
            <!--我要续费-->
            <div class="aui-info">
              <router-link to="/center/myshebei1/shebeikongzhi1">
                <div class="aui-info-item">{{items.deviceNumber}}</div>
              </router-link>

              <router-link to="/center/myshebei1/xufei">
                <div class="aui-info-item z-shebei-mar-r">
                  <img src="../../images/center_zhengchang.png" style="width:.9rem">
                  <span class="aui-margin-l-10 aui-margin-r-10">
                    正常
                    <span class="z-myshebei-span">续费</span>
                  </span>
                </div>
                <!--<div class="aui-info-item z-shebei-mar-r">-->
                <!--<img src="../../images/center_xufei.png" style="width:.9rem">-->
                <!--<span class="aui-margin-l-10 aui-margin-r-10">续费</span>-->
                <!--</div>-->
              </router-link>
            </div>


          </div>


        </div>
      </template>


    </div>

  </div>
</template>

<script>

  import axios from 'axios'

  export default {
    name: "myshebei1",
    data() {
      return {
        userId: '1',
        pageNum: '2',
        pageSize: '2',
        lists: []
      }
    },
    created: function () {
      this.myDevice();
    },
    methods: {
      myDevice: function () {
        //初始化加载的数据
        var data = new FormData();
        data.append('userId', this.userId);
        data.append('pageNum', this.pageNum);
        data.append('pageSize', this.pageSize);

        //发送接口请求
        axios
          .post('http://xiwanji.91xiaokong.com//selectToDevice', data)
          .then(res => {

            this.lists = res.data;
            console.log(JSON.stringify(res.data))

          })
      }
    }
  }
</script>

<style scoped>

</style>



