<template>
    <div class="daiyan">
      <!--头部区域选择-->
      <header class="aui-bar aui-bar-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12">我为代言</div>
      </header>
      <!--轮播图 1-->

      <swiper :options="swiperOption">

        <swiper-slide><router-link to="daiyanDetail"><img src="../../images/banner02.png"></router-link></swiper-slide>
        <swiper-slide><router-link to="daiyanDetail"><img src="../../images/banner03.png"></router-link></swiper-slide>
        <swiper-slide><router-link to="daiyanDetail"><img src="../../images/banner04.png"></router-link></swiper-slide>
        <swiper-slide><router-link to="daiyanDetail"><img src="../../images/banner05.png"></router-link></swiper-slide>
        <swiper-slide><router-link to="daiyanDetail"><img src="../../images/banner06.png"></router-link></swiper-slide>


      </swiper>

      <!--以下看需要添加-->
      <div class="swiper-scrollbar" style="background: red;"></div>
      <div class="swiper-button-next"></div>
      <div class="swiper-button-prev"></div>
      <div class="swiper-pagination"></div>



      <!--轮播图2-->

      <swiper :options="swiperOptionTwo" style="margin-top: 20px;">

        <swiper-slide><router-link to="daiyanDetail"><img src="../../images/banner07.png"></router-link></swiper-slide>
        <swiper-slide><router-link to="daiyanDetail"><img src="../../images/banner08.png"></router-link></swiper-slide>
        <swiper-slide><router-link to="daiyanDetail"><img src="../../images/banner09.png"></router-link></swiper-slide>
        <swiper-slide><router-link to="daiyanDetail"><img src="../../images/banner10.png"></router-link></swiper-slide>
        <swiper-slide><router-link to="daiyanDetail"><img src="../../images/banner06.png"></router-link></swiper-slide>


      </swiper>

      <!--以下看需要添加-->
      <div class="swiper-scrollbarTwo" style="background: red;"></div>
      <div class="swiper-button-next"></div>
      <div class="swiper-button-prev"></div>
      <div class="swiper-pagination"></div>
      <!--添加现在拍照或是从相机中选择-->
      <div style="width: 100%;position: fixed;bottom: 0;margin: auto;bottom: 10px;">
        <div class="aui-btn aui-btn-info aui-btn-block aui-btn-sm aui-margin-t-15 btn-bg">现在拍照或是从相机中选择</div>

      </div>
    </div>
</template>

<script>
  import '../../css/swiper-3.4.2.min.css';
  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  import 'swiper/dist/css/swiper.css'
    export default {
        name: "daiyan",
      data(){
          return{
            //轮播图1
            swiperOption: {//swiper3
              autoplay:2000,
              speed: 5000,
              loop:true,

              autoplay: {
                delay: 2000, //自动切换的时间间隔，单位ms
                disableOnInteraction: true, //用户操作swiper之后，是否禁止autoplay。
                reverseDirection: false, //开启反向自动轮播。
                waitForTransition: true, //等待过渡完毕。自动切换会在slide过渡完毕后才开始计时。
              },
              //点击箭头进行播放
              navigation: {
                nextEl: '.swiper-button-next', //前进按钮的css选择器或HTML元素。
                prevEl: '.swiper-button-prev', //后退按钮的css选择器或HTML元素。
                hideOnClick: true, //点击slide时显示/隐藏按钮
                disabledClass: 'my-button-disabled', //前进后退按钮不可用时的类名。
                hiddenClass: 'my-button-hidden', //按钮隐藏时的Class
              },
              //滑动播放
              scrollbar: {
                el: '.swiper-scrollbar',
                hide: true, //滚动条是否自动隐藏。默认：false，不会自动隐藏。
                draggable: true, //该参数设置为true时允许拖动滚动条。
                snapOnRelease: true, //如果设置为false，释放滚动条时slide不会自动贴合。
                dragSize: 10, //设置滚动条指示的尺寸。默认'auto': 自动，或者设置num(px)。
              },

            },


            //轮播图2
            swiperOptionTwo: {//swiper3
              autoplay:2000,
              speed: 1000,
              loop:true,

              autoplay: {
                delay: 1000, //自动切换的时间间隔，单位ms
                disableOnInteraction: true, //用户操作swiper之后，是否禁止autoplay。
                reverseDirection: false, //开启反向自动轮播。
                waitForTransition: true, //等待过渡完毕。自动切换会在slide过渡完毕后才开始计时。
              },
              //点击箭头进行播放
              navigation: {
                nextEl: '.swiper-button-next', //前进按钮的css选择器或HTML元素。
                prevEl: '.swiper-button-prev', //后退按钮的css选择器或HTML元素。
                hideOnClick: true, //点击slide时显示/隐藏按钮
                disabledClass: 'my-button-disabled', //前进后退按钮不可用时的类名。
                hiddenClass: 'my-button-hidden', //按钮隐藏时的Class
              },
              //滑动播放
              scrollbar: {
                el: '.swiper-scrollbarTwo',
                hide: true, //滚动条是否自动隐藏。默认：false，不会自动隐藏。
                draggable: true, //该参数设置为true时允许拖动滚动条。
                snapOnRelease: true, //如果设置为false，释放滚动条时slide不会自动贴合。
                dragSize: 10, //设置滚动条指示的尺寸。默认'auto': 自动，或者设置num(px)。
              },

            }

          }
      },
    }
</script>

<style scoped>
  /*轮播图*/
  .swiper-container{
    width: 100%;
    height: auto;
  }
  .swiper-button-next, .swiper-button-prev {
    position: absolute;
    top: 24%;
    width: 27px;
    height: 22px;
    /* margin-top: -22px; */
    z-index: 10;
    cursor: pointer;
    background-size: 8px 25px;
    background-position: center;
    background-repeat: no-repeat;
    color: #ddd !important;
  }

</style>



