<template>
    <div class="editAdress">
      <!--头部-->
      <header class="aui-bar aui-bar-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12">编辑地址</div>
      </header>
      <!--内容区-->
      <ul class="aui-list aui-form-list">


          <li class="aui-list-item">
            <div class="aui-list-item-inner">
              <div class="aui-list-item-input">
                <input type="text" class="aui-font-size-12"   v-model="addressInfo.addressName"  placeholder="请输入用户名">
              </div>
            </div>
          </li>
          <li class="aui-list-item">
            <div class="aui-list-item-inner">

              <div class="aui-list-item-input">
                <input type="number" class="aui-font-size-12" v-model="addressInfo.addressPhone" placeholder="请输入手机号">
              </div>
            </div>
          </li>
          <!--后期做地区弹框的效果-->
          <!--<li class="aui-list-item">-->

          <!--<div class="aui-list-item-inner aui-list-item-arrow">-->
          <!--<div class="aui-list-item-title aui-font-size-12">所在地区</div>-->
          <!--</div>-->
          <!--</li>-->
          <li class="aui-list-item" style="border-bottom: 0.5px solid #f8f8f8;">
            <div class="aui-list-item-inner">

              <div class="aui-list-item-input">
                <input type="text" class="aui-font-size-12" v-model="addressInfo.addressAddress" placeholder="请输入详细地址">
              </div>
            </div>
          </li>

          <li class="aui-list-item">
            <div class="aui-list-item-inner">
              <div class="aui-list-item-label aui-font-size-12" @click="addressState">
                设为默认地址
              </div>
              <div class="aui-list-item-input">
                <input type="checkbox" class="aui-switch z-switch" v-model="addressInfo.addressDefaultState"  style="margin-left: 190px;">
              </div>
            </div>
          </li>



      </ul>

      <div class="aui-btn aui-btn-info aui-btn-block aui-btn-sm aui-margin-t-15 btn-bg" @click="updateToAddress($event)">保存</div>

    </div>
</template>

<script>
  import axios from 'axios'
    export default {
        name: "editAdress",
        data(){
          return{
            userId:'1',
            addAdressList:[],
            //注释：不需要再重新定义相应的变量，直接接收由上面传过来的对象
            // addressName:'',
            // addressUserId:'1',
            // addressPhone:'',
            // addressDefaultState:'',
            // addressAddress:'',
            // addressId:'',

            addressInfo:{}


          }
      },
      created: function () {
          //主要是接收地址列表中传入的参数对象
        this.addressInfo=this.$route.query.addressInfo;
        console.log(JSON.stringify(this.addressInfo));

        //{"addressId":4,"addressName":"张昊然","addressPhone":"15838149734","addressDefaultState":"1","addressAddress":"河南郑州市金水区农业路碧隆小区6号楼2单元403室","addressDeleteState":"0","addressUserId":1}
      },
      methods:{
        //  点击开关的默认事件
        addressState(){
          this.addressDefaultState =  !this.addressDefaultState;

        },


      //点击提交保存事件
        updateToAddress:function (event) {
          event.preventDefault();
          var data = new FormData();
          //注释：需要把上一界面传过来的值赋值到点击更改相应对象中
          data.append('addressName', this.addressInfo.addressName);
          data.append('addressPhone', this.addressInfo.addressPhone);

          data.append('addressAddress', this.addressInfo.addressAddress);

          data.append('addressUserId', this.addressInfo.addressUserId);

          data.append('addressDefaultState',Number(this.addressInfo.addressDefaultState) );
          var config ={
            headers: {
              'Content-Type': 'multipart/form-data'
            }
          }
          var reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
          //逻辑处理

          if (this.addressInfo.addressPhone == '') {
            alert("请输入手机号码");
            return;
          } else if (!reg.test(this.addressInfo.addressPhone)) {
            alert("手机格式不正确");
            return;
          }
          axios
            .post('http://xiwanji.91xiaokong.com/updateToAddress',data,config)
            .then(res=>{
              if(res.data.status == 200){
                alert(JSON.stringify(res.data.message));

              }else {
                alert(JSON.stringify(res.data.message));
              }
            })


        }


      },
    }
</script>

<style scoped>

</style>



