<template>
    <div class="mytuanyou">
      <!--头部-->
      <header class="aui-bar aui-bar-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12">我的团友</div>
      </header>
      <!--我的团友人数-->
      <div class="aui-list aui-form-list">
        <div class="aui-list-header">我的团友共计:24人</div>

      </div>
      <!--我的团友信息-->
      <template v-for="items in lists.list">
        <div class="aui-card-list z-margin-b0 z-border-b1" v-for="">
          <div class="userinfo-header">
            <div class="aui-info">
              <div class="aui-info-item z-margin-l5">
                <img src="../../images/zhuzhu.png" style="width:2.2rem">
                <span class="aui-margin-l-10 aui-margin-r-10">{{items.userWxName}}</span>
              </div>
              <div class="aui-info-item z-tuan-margin-r8 aui-font-size-12 colorBlack">注册时间:{{items.userDate}}</div>
            </div>
          </div>
          <!--<div class="aui-card-list-content-padded" style="margin-top: -15px;margin-left: 40px;">注册时间：2019-01-22</div>-->
        </div>
      </template>


      <router-view></router-view>
    </div>
</template>

<script>
  import axios from 'axios'
    export default {
        name: "mytuanyou",
      data(){
        return{
          userId:'1',
          pageNum:'1',
          pageSize:'2',
          lists:[]
        }
      },
      created:function () {
        this.userTeam();
      },
      methods:{
        userTeam:function () {
          //初始化加载的数据
          var data = new FormData();
          data.append('userId', this.userId);
          data.append('pageNum', this.pageNum);
          data.append('pageSize', this.pageSize);

          //发送接口请求
          axios
            // .post('http://140.143.200.89:8080/dishwasher/selectByUserTeam',data)
            .post('http://xiwanji.91xiaokong.com//selectByUserTeam',data)
            .then(response =>{
              console.log(JSON.stringify(response))
              this.lists= response.data;
              console.log(JSON.stringify(response.data))

            })
        }
      }
    }
</script>

<style scoped>

</style>



