import Vue from 'vue'
import Router from 'vue-router'
import index from '../components/index/index.vue'
import daiyanDetail from '../components/index/daiyanDetail.vue'
import daiyan from '../components/index/daiyan.vue'
import addAdress from '../components/index/addAdress.vue'
import addressList from '../components/index/addressList.vue'
import goods_detail from '../components/index/goods_detail.vue'
import goods_detailI from '../components/index/goods_detailI.vue'
import confirm_order from '../components/index/confirm_order.vue'
import confirm_orderI from '../components/index/confirm_orderI.vue'
import indexI from '../components/index/indexI.vue'
import editAdress from '../components/index/editAdress.vue'
import center from '../components/center/center.vue'
import pintuanma from '../components/center/pintuanma.vue'
import duanxinyanzheng from '../components/center/duanxinyanzheng.vue'
import center1 from '../components/center/center1.vue'
import xufeijilu from '../components/center/xufeijilu.vue'
import addzhanghu from '../components/center/addzhanghu.vue'
import addzhanghuI from '../components/center/addzhanghuI.vue'
import addshoukuangfashi from '../components/center/addshoukuangfashi.vue'
import addshoukuangfashiI from '../components/center/addshoukuangfashiI.vue'
import saoma from '../components/saoma/saoma.vue'
import saomaI from '../components/saoma/saomaI.vue'
import saosao from '../components/saoma/saosao.vue'
import saosaoI from '../components/saoma/saosaoI.vue'
import xufei from '../components/saoma/xufei.vue'
import jifentixian from '../components/center/jifentixian.vue'
import jifentixian1 from '../components/center/jifentixian1.vue'
import jifenmingxi from '../components/center/jifenmingxi.vue'
import jifenmingxi1 from '../components/center/jifenmingxi1.vue'
import tixian from '../components/center/tixian.vue'
import tixianjilu1 from '../components/center/tixianjilu1.vue'
import tixianjilu from '../components/center/tixianjilu.vue'
import mytuanyou from '../components/center/mytuanyou.vue'
import myshebei from '../components/center/myshebei.vue'
import myshebei1 from '../components/center/myshebei1.vue'
import tuihuoshenqing1 from '../components/center/tuihuoshenqing1.vue'
import shebeikongzhi1 from '../components/center/shebeikongzhi1.vue'
import shebeikongzhi from '../components/center/shebeikongzhi.vue'

import test from '../components/test'


Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '',
      component: indexI,
    },

    {
      path: '/center',
      component: center,
        children:[
          {
            path: '',
            component: center1
          },
          {
            path: 'jifentixian1',
            component: jifentixian1,
            children:[
              {
                path: '',
                component: jifentixian,
              },
              //提现记录
              {
                path: 'tixianjilu1',
                component: tixianjilu1,
                children:[
                     {
                      path: '',
                      component: tixianjilu,
                     }
                  ]
              },
              //积分明细
              {
                path: 'jifenmingxi1',
                component: jifenmingxi1,
                children:[
                  {
                    path: '',
                    component: jifenmingxi,
                  }
                ]
              },
            //  添加账户
              {
                path: 'addzhanghu',
                component: addzhanghuI,
              },
              //  添加账户收款方式
              {
                path: 'addshoukuangfashi',
                component: addshoukuangfashiI,
              },

            ],

          },
          //我的团友
          {
            path: 'mytuanyou',
            component: mytuanyou
          },
          //我的设备
          {
            path: 'myshebei1',
            component: myshebei1,
            children:[
              {
                path: '',
                component: myshebei,
              },
              // //设备控制
              {
                path: 'shebeikongzhi1',
                component: shebeikongzhi1,

                children:[
                  {
                    path: '',
                    component: shebeikongzhi,

                  },
                ]
              },

              //我的续费
              {
                path: 'xufei',
                component: xufei

              },
            ]

          },
          //退货申请
          {
            path: 'tuihuoshenqing1',
            component: tuihuoshenqing1,

          },
          //提现
          {
            path: 'tixian',
            component: tixian
          },
          //短信验证
          {
            path: 'duanxinyanzheng',
            component: duanxinyanzheng
          },

          //我的拼团码
          {
            path: 'pintuanma',
            component: pintuanma
          },
          //我的拼团码
          {
            path: 'pintuanma',
            component: pintuanma
          },
          //续费记录
          {
            path: 'xufeijilu',
            component: xufeijilu

          },

        ]

    },

    {
      path: '/saoma',
      component: saoma,
      children:[
        {
          path: '',
          component: saomaI
        },
        {
          path: 'xufei',
          component: xufei
        },

        {
          path: 'saosao',
          component: saosaoI
        },
        {
          path: 'center',
          component: center1
        },

    ]

    },
    {
      path: '/index',
      component: index,
      children:[
          {
            path: '',
            component: indexI
          },
        //商品详情
        {
          path: 'goods_detail',
          component: goods_detail,
          children:[
            {
              path: '',
              component: goods_detailI,
            }
          ]
        },
        //确认订单
        {
          path: 'confirm_order',
          component: confirm_order,
          children:[
            {
              path: '',
              component: confirm_orderI,
            }
          ]
        },
        //地址列表
        {
          path: 'addressList',
          component: addressList,

        },
        //添加地址
        {
          path: 'addAdress',
          component: addAdress,

        },
        //代言
        {
          path: 'daiyan',
          component: daiyan,

        },
        //代言详情
        {
          path: 'daiyanDetail',
          component: daiyanDetail,

        },
        //个人中心
        {
          path: 'center',
          component: center1
        },
        //编辑地址
        {
          path: 'editAdress',
          component: editAdress
        },

        ]

    },


    {
      path: '/test',
      component: test,
      children:[

        {
          path: '',
          component:indexI,

        },

        {
          path: 'saoma',
          component: saomaI,

        },
        {
          path: 'center',
          component: center1
        },

      ]

    },

  ]
})



