<template>
    <div class="tixian">
      <!--头部-->
      <header class="aui-bar aui-bar-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12">提现</div>
      </header>
      <!--内容区-->
      <ul class="aui-list aui-list-in z-margin-t5 z-background-image">
        <li class="aui-list-item">
          <!--注释后期做：选择账号类型-->
          <!--<div class="aui-list-item-inner aui-list-item-arrow">-->
            <!--<div class="aui-list-item-title aui-font-size-12">选择收款账号</div>-->
          <!--</div>-->

          <div class="aui-list-item-inner">
            <div class="aui-list-item-input">
              <input type="text" class="aui-font-size-12" v-model="accountId" name="" placeholder="填写添加的账号">
            </div>
          </div>

        </li>
        <li class="aui-list-item">
          <div class="aui-list-item-inner">
            <div class="aui-list-item-label" style="width: 8%;">
              ￥
            </div>
            <div class="aui-list-item-input">
              <input class="aui-font-size-12" type="number" v-model="money" placeholder="请输入需要提现的金额">
            </div>
          </div>
        </li>
      </ul>

      <div class="aui-card-list-content-padded aui-padded-t-5 aui-padded-b-5 z-margin-t1 aui-font-size-12">
        可以提现:100
      </div>
      <div class="aui-card-list-content-padded aui-padded-t-5 aui-padded-b-5 z-margin-t1 aui-font-size-12">
        总积分：1200
      </div>
      <div class="aui-card-list-content-padded aui-padded-t-5 aui-padded-b-5 z-margin-t1 fontcolor aui-font-size-12">
        温馨提示:最低提现金额￥100
      </div>

      <!--底部进行提交-->
      <div style="width: 100%;position: absolute;margin: auto;top: 300px;">
        <div class="aui-btn aui-btn-info aui-btn-block aui-btn-sm aui-margin-t-15 btn-bg" @click="cashSubmit($event)">提交</div>
      </div>

    </div>
</template>

<script>
  import axios from 'axios'

  export default {
        name: "tixian",
      data(){
          return{
            accountId:'',
            userId:'1',
            money:'',
          }
      },
      created:function () {

      },
      methods:{
        //点击提现事件
        cashSubmit:function(){
          event.preventDefault();
          var data = new FormData();

          data.append('accountId', this.accountId);
          data.append('userId', this.userId);
          data.append('money', this.money);

          var config ={
            headers: {
              'Content-Type': 'multipart/form-data'
            }
          }
            if (this.userId == '') {
            alert('请先登录账号');
            return;
          } else if (this.accountId == '') {
            alert("请输入账号");
            return;
          } else if (this.money == '') {
              alert("请输入金额");
              return;
            }
          axios
            // .post('http://140.143.200.89:8080/dishwasher/cash',data,config)
            .post('http://xiwanji.91xiaokong.com/cash',data,config)
            .then(res =>{
              if(res.data.status == 200){
                alert(JSON.stringify(res.data.message));

              }else {
                alert(JSON.stringify(res.data.message));
              }
            })






        }

      }
    }
</script>

<style scoped>
  .aui-btn-info.aui-active, .aui-btn-info:active {
    color: #fff !important;
    background-color: #ff5d47 !important;
  }

</style>



