<template>
    <div class="addAdress">
      <!--头部-->
      <header class="aui-bar aui-bar-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12">添加地址</div>
      </header>
      <!--内容区-->
      <ul class="aui-list aui-form-list" style="">

        <li class="aui-list-item">
          <div class="aui-list-item-inner">
            <div class="aui-list-item-input">
              <input type="text" class="aui-font-size-12" v-model="addressName" placeholder="请输入你的姓名">
            </div>
          </div>
        </li>
        <li class="aui-list-item">
          <div class="aui-list-item-inner">

            <div class="aui-list-item-input">
              <input type="number" class="aui-font-size-12" v-model="addressPhone" placeholder="请输入手机号">
            </div>
          </div>
        </li>
        <!--后期做地区弹框的效果-->
        <!--<li class="aui-list-item">-->

        <!--<div class="aui-list-item-inner aui-list-item-arrow">-->
        <!--<div class="aui-list-item-title aui-font-size-12">所在地区</div>-->
        <!--</div>-->
        <!--</li>-->
        <li class="aui-list-item" style="border-bottom: 0.5px solid #f8f8f8;">
          <div class="aui-list-item-inner">

            <div class="aui-list-item-input">
              <input type="text" class="aui-font-size-12" v-model="addressAddress" placeholder="请输入详细地址">
            </div>
          </div>
        </li>

        <li class="aui-list-item">
          <div class="aui-list-item-inner">
            <div class="aui-list-item-label aui-font-size-12">
              设为默认地址
            </div>
            <div class="aui-list-item-input" @click="addressState">
              <input type="checkbox" class="aui-switch z-switch" style="margin-left: 190px;">
            </div>
          </div>
        </li>
        <!--<li class="aui-list-item">-->
          <!--<div class="aui-list-item-inner">-->
            <!--<div class="aui-list-item-label aui-font-size-12">-->
              <!--设为默认地址-->
            <!--</div>-->
            <!--<div class="aui-list-item-input">-->
              <!--<input type="checkbox" class="aui-switch z-switch" v-model="addressDefaultState" checked="" style="margin-left: 190px;">-->
            <!--</div>-->
          <!--</div>-->
        <!--</li>-->

      </ul>

      <div class="aui-btn aui-btn-info aui-btn-block aui-btn-sm aui-margin-t-15 btn-bg" @click="insertAddress($event)">保存</div>

    </div>
</template>

<script>
  import axios from 'axios'
    export default {
        name: "addAdress",
      data(){
          return{
            addressName:'',
            addressPhone:'',
            addressAddress:'',
            //不是太清楚怎样获取用户的id
            addressUserId:'1',
            addressDefaultState:1,

          }
      },
      created:function(){

      },
      methods:{

        //  点击开关的默认事件
        addressState(){
          this.addressDefaultState =  !this.addressDefaultState;

        },
        //  提交保存事件
        insertAddress:function (event) {
          event.preventDefault();
          var data = new FormData();
          data.append('addressName', this.addressName);
          data.append('addressPhone', this.addressPhone);

          data.append('addressAddress', this.addressAddress);

          data.append('addressUserId', this.addressUserId);

          data.append('addressDefaultState',Number(this.addressDefaultState) );
          var config ={
            headers: {
              'Content-Type': 'multipart/form-data'
            }
          }
          var reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
          //逻辑处理

         if (this.addressPhone == '') {
            alert("请输入手机号码");
            return;
          } else if (!reg.test(this.addressPhone)) {
            alert("手机格式不正确");
            return;
          }

          axios
            .post('http://xiwanji.91xiaokong.com/insertAddress',data,config)
            .then(res=>{
              console.log(JSON.stringify(res));
              if(res.data.status == 200){
                alert(JSON.stringify(res.data.message));

              }else {
                alert(JSON.stringify(res.data.message));
              }
            })


        }
      },

    }
</script>

<style scoped>

</style>



