        <div class="aui-card-list-header aui-card-list-user bg-background">

          <div class="z-shebei-box1" style="border-bottom: none;">
            <div class="aui-card-list-user-name">
              <div>{{items.deviceName}}6</div>
              <div class="aui-font-size-14 text-light">设备状态</div>
            </div>
            <div class="z-shebei-xian">
              <p></p>
            </div>
            <div class="userinfo-header">
              <div class="aui-info">
                <div class="aui-info-item">{{items.deviceNumber}}</div>
                <!--正常的状态-->
                <div class="aui-info-item z-shebei-mar-r" v-if="items.deviceStatus ==0">
                  <img src="../../images/center_zhengchang.png" style="width:.9rem">
                  <span class="aui-margin-l-10 aui-margin-r-10">正常</span>

                </div>
                <!--续费的状态-->
                <div class="aui-info-item z-shebei-mar-r" v-if="items.deviceStatus ==1">
                  <img src="../../images/center_xufei.png" style="width:.9rem">
                  <span class="aui-margin-l-10 aui-margin-r-10">续费</span>

                </div>
              </div>
            </div>
          </div>

          <!--下次续费时间-->
          <div class="aui-list-item  z-sbkz-rq">
            <div class="aui-list-item-inner">
              <div class="aui-list-item-label-icon aui-font-size-12">
                下次设备续费日期:{{items.deviceDate}}
              </div>

            </div>

          </div>

        </div>
        <!--开机续费记录-->
        <div class="aui-content aui-grid">
          <div class="aui-row">
            <div class="aui-col-xs-4 aui-border-r">
              <div class="aui-gird-lable aui-font-size-12">开机</div>
            </div>
            <div class="aui-col-xs-4 aui-border-r">
              <div class="aui-gird-lable aui-font-size-12">关机</div>
            </div>
            <!--<a href="./tuihuoshenqing.html">-->
            <router-link to="/center/tuihuoshenqing1">
              <div class="aui-col-xs-4">
                <div class="aui-gird-lable aui-font-size-12">退货</div>
              </div>
            </router-link>
            <!--</a>-->

          </div>
        </div>

        <!--公用部分  设备日志-->
        <div class="z-guding-xian" style="">
          <p class="z-guding-xian-p1" style=""></p>
          <p class="z-guding-xian-p2" style="">设备日志</p>
        </div>
        <div class="bg-background">
          <div class="z-shebei-box1">
            <div class="aui-card-list-user-name">
              <div>
                <span>20190201</span>
                <span>13:01</span>
              </div>
              <div class="aui-font-size-14 text-light">开机时间</div>
            </div>


          </div>

          <div class="z-shebei-box1">
            <div class="aui-card-list-user-name">
              <div>
                <span>20190201</span>
                <span>13:01</span>
              </div>
              <div class="aui-font-size-14 text-light">续费时间</div>
            </div>

          </div>
          <div class="z-shebei-box1">
            <div class="aui-card-list-user-name">
              <div>
                <span>20190201</span>
                <span>13:01</span>
              </div>
              <div class="aui-font-size-14 text-light">续费时间</div>
            </div>


          </div>
          <div class="z-shebei-box1">
            <div class="aui-card-list-user-name">
              <div>
                <span>20190201</span>
                <span>13:01</span>
              </div>
              <div class="aui-font-size-14 text-light">续费时间</div>
            </div>


          </div>
          <div class="z-shebei-box1">
            <div class="aui-card-list-user-name">
              <div>
                <span>20190201</span>
                <span>13:01</span>
              </div>
              <div class="aui-font-size-14 text-light">续费时间</div>
            </div>


          </div>
          <div class="z-shebei-box1">
            <div class="aui-card-list-user-name">
              <div>
                <span>20190201</span>
                <span>13:01</span>
              </div>
              <div class="aui-font-size-14 text-light">续费时间</div>
            </div>


          </div>
          <div class="z-shebei-box1">
            <div class="aui-card-list-user-name">
              <div>
                <span>20190201</span>
                <span>13:01</span>
              </div>
              <div class="aui-font-size-14 text-light">续费时间</div>
            </div>
          </div>
        </div>

      </template>

    </div>
</template>

<script>

    import axios from 'axios'
    export default {
      name: "shebeikongzhi",
      data(){
        return{
          deviceId:'1',
          lists:[]
        }
      },
      created:function () {
        this.deviceRecord();
      },
      methods:{
        deviceRecord:function () {
          //初始化加载的数据
          var data = new FormData();
          data.append('deviceId', this.deviceId);


          //发送接口请求
          axios
            .post('http://xiwanji.91xiaokong.com//selectToDeviceRecord',data)
            .then(response =>{
              // console.log(JSON.stringify(response))
              this.lists= response.data;
             // console.log(JSON.stringify(response.data))

            })
        }
      }
    }
</script>

<style scoped>
  .z-sbkz-rq{padding-top: 15px;padding-bottom: 15px;padding-left: 20px;font-size: 14px;border-bottom: 1px solid #f5f5f5;border-top:1px solid #f5f5f5; }

</style>



