<template>
    <div class="confirm_orderI bg-content">
      <div class="aui-list aui-media-list">
        <!--头部-->
        <header class="aui-bar aui-bar-nav" id="aui-header">
          <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
            <span class="aui-iconfont aui-icon-left"></span>
          </a>
          <div class="aui-title aui-font-size-12">确认订单</div>
        </header>

      </div>

      <!--收货地址-->
      <div class="aui-list aui-list-in z-background-image">

        <div class="aui-list-item z-background-image" style="">

          <router-link to="./addressList">
            <div class="aui-list-item-media" style=" width: 10px;display: inline-block;">
              <i class="aui-iconfont aui-icon-location aui-font-bod black"></i>
            </div>

            <!--显示默认地址和显示已经修改的地址的判断主要是根据addressId进行来判断的-->
            <template v-if="orderAddress.address">
              <div class="aui-list-item-arrow z-order-margin" v-if="orderAddress.address.addressId>0">
                <div class="aui-list-item-text">
                  <div class="aui-list-item-title aui-font-size-14">{{orderAddress.address.addressName}} {{orderAddress.address.addressPhone}}</div>
                </div>
                <div class="aui-list-item-text aui-font-size-12 aui-ellipsis-2">
                  {{orderAddress.address.addressAddress}}
                </div>
              </div>

            </template>

            <template v-else>
              <div class="aui-list-item-arrow z-order-margin">
                <div class="aui-list-item-title aui-font-size-14">请选择一个收货地址</div>
              </div>
            </template>


          </router-link>

        </div>

      </div>
      <!--商品内容-->
      <div>
        <div class="z-margin-t1">
          <!--主要是遍历购买多数商品-->
          <div class="aui-card-list aui-border-t aui-border-b   z-margin-b1 z-shop">
            <div class="aui-card-list-header aui-card-list-user" v-if="goodsDetail.goodsImgList">
              <div class="aui-card-list-user-avatar" style="width: 4rem !important;">
                <img :src="'http://'+goodsDetail.goodsImgList[0].goodsImgImg">
              </div>
              <div class="aui-card-list-user-name">
                <div class="aui-font-size-12 aui-ellipsis-2" style="width: 95%;">{{goodsDetail.goodsTitle}}111</div>

              </div>
            </div>
            <div class="aui-card-list-content aui-border-t z-background-image" style="margin-left: 3.5rem;">
              <div class="aui-list aui-list-noborder" style="background-color: transparent;">
                <div class="aui-list-item aui-padded-l-0">
                  <div class="aui-list-item-inner">
                    <div class="aui-list-item-title  z-order-font-color" style="">
                      <span>积分:</span>
                      <span>{{goodsDetail.goodsCurrnetScore}}</span>
                    </div>
                    <div class="aui-list-item-right goods-price z-margin-r1 aui-font-size-14 z-order-font-color">x {{count}}</div>
                  </div>
                </div>
              </div>

            </div>
            <!--购买数量-->
            <div class="aui-list aui-list-in z-padding-t1 z-padding-b1">
              <div class="aui-list-item z-background-image">

                <div class="aui-list-item-inner z-background-image">
                  <div class="aui-list-item-title aui-font-size-12">购买数量</div>
                  <div class="aui-list-item-right">
                    <div class="aui-list-item-text aui-font-size-12 z-cart-content-count">
                      <div class="aui-bar aui-bar-btn z-cart-count-bar" type="count" style="margin-top: -10px;">
                        <div class="aui-bar-btn-item aui-font-size-12 active z-com-border" v-on:click="subtract()" tapmode="" data-item-order="0">
                          <i class="aui-iconfont aui-icon-minus"></i>
                        </div>
                        <div class="aui-bar-btn-item z-com-border" tapmode="" data-item-order="1">
                          <input type="number" class="aui-input aui-text-center" v-model="count"  value="1">
                        </div>
                        <div class="aui-bar-btn-item aui-font-size-12 z-com-border" v-on:click="add()" tapmode="" data-item-order="2">
                          <i class="aui-iconfont aui-icon-plus"></i>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

            </div>

          </div>


        </div>

        <!--共计4件商品：合计454积分-->
        <div class="aui-list aui-list-in z-margin-t1 z-background-image">

          <div class="aui-list-item z-background-image">

            <div class="aui-list-item-inner">

              <div class="aui-list-item-right colorBlack" style="max-width: 100% !important; margin-left:115px;">
                共计: <span class="fontcolor aui-font-size-14 z-colorshenhong">{{count}}</span> 件商品
                合计: <span class="fontcolor aui-font-size-14 z-colorshenhong">{{goodsDetail.goodsCurrnetScore*count}} </span>积分
              </div>
            </div>
          </div>
        </div>


        <!--买家留言-->
        <div class="aui-content z-margin-t1 z-background-image">
          <div class="aui-list aui-form-list z-background-image">
            <div class="aui-list-item">
              <div class="aui-list-item-inner">
                <div class="aui-list-item-label aui-font-size-12">
                  留言:
                </div>
                <div class="aui-list-item-input aui-font-size-12" style=" margin-left: -55px;">
                  <input class="aui-font-size-12" v-model="orderMessage" type="text" placeholder="请输入50个字以内">
                </div>
              </div>
            </div>
          </div>
        </div>



        <!--运费-->
        <div class="aui-list aui-list-in z-background-image">
          <div class="aui-list-item z-background-image">

            <div class="aui-list-item-inner">
              <div class="aui-list-item-title aui-font-size-12">运费</div>
              <div class="aui-list-item-right">包邮</div>
            </div>
          </div>

        </div>
        <!--暂时注释 日期：2019-03-07-->
        <!--&lt;!&ndash;选择积分支付方式&ndash;&gt;-->
        <!--<div class="aui-list aui-list-in z-background-image z-margin-t2">-->
          <!--<div class="aui-list-item z-background-image" style="position: relative">-->

            <!--<div class="aui-list-item-inner">-->
              <!--<div class="aui-list-item-title aui-font-size-12">使用积分支付</div>-->
              <!--<div class="aui-list-item-right">-->
                <!--<div class="aui-content-padded fontcolor" style="position: absolute;top: -25px;right: 0px;">-->
                  <!--<label><input class="aui-radio" type="radio" name="demo" checked=""></label>-->

                <!--</div>-->
              <!--</div>-->
            <!--</div>-->
          <!--</div>-->

        <!--</div>-->

        <!--积分使用说明-->
        <!--<div class="aui-list aui-list-in">-->
          <!--<div class="aui-list-item z-background-image">-->

            <!--<div class="aui-list-item-inner">-->
              <!--<div class="aui-list-item-title aui-font-size-12">-->
                <!--<p>通用积分：<span class="fontcolor">0.00</span></p>-->
                <!--<p class="fontcolor">商品价格大于积分余额自动跳转微信支付不足余额</p>-->
              <!--</div>-->
              <!--<div class="aui-list-item-right"></div>-->
            <!--</div>-->
          <!--</div>-->

        <!--</div>-->


      </div>

      <!--底部-->
      <footer class="aui-bar aui-bar-tab aui-margin-t-15" style="bottom: 0;">
        <div class="aui-bar-tab-item " tapmode="" style="width:12rem;">
          <span class="aui-font-size-14 colorBlack">实付款：</span>
          <span class="fontcolor aui-font-size-14 z-colorshenhong">{{goodsDetail.goodsCurrnetScore*count}} </span>
          <span class="aui-font-size-14 colorBlack">积分</span>

        </div>

        <div class="aui-bar-tab-item z-backgroundshenju" @click="orderSubmit" tapmode=""  style="width: auto;">

          <div class="aui-bar-tab-label colorWhite">提交订单</div>
        </div>


      </footer>

    </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: "confirm_orderI",
    data(){
      return{
        goodsId:'',
        goodsDetail:{},
        userId:'1',
        orderAddress:'',
        count: 1,

        orderMessage:'',
        orderTotalPrice:'',
        orderAddressId:'',
        orderUserId:1,


      }
    },
    created:function(){
      //初始化加载订单详情信息
      this.oderDetail();
    //  调用用户默认的地址信息
      this.orderAddressCheckOne();
    },
    methods:{
      //商品订单详情
      oderDetail:function () {
        //接收从商品详情页中传来的商品id的字段
        var newsID=this.$route.query.goodsImgGoodsId;
        axios
          .post('http://xiwanji.91xiaokong.com//orderGoodsCheck?goodsId='+newsID)
          .then(response =>{
            this.goodsDetail=response.data


          })

      },

    //  直接获取用户添加的地址信息
      orderAddressCheckOne:function(){
        var data = new FormData();
        data.append('userId', this.userId);
        axios
          // .post('http://140.143.200.89:8080/dishwasher/orderAddressCheck1',data)
          .post('http://xiwanji.91xiaokong.com/orderAddressCheck1',data)
          .then(res=>{
            this.orderAddress = res.data;

            // console.log(JSON.stringify(this.orderAddress.address.addressId));
          })

      },

      //点击添加购买商品数量事件
      //实现数字增加
      add: function(count) {
        this.count++;
      },
      //数字减少，如果少于0则弹出不能再少
      subtract: function(count) {
        if (this.count <= 1) {
          alert("不能少了")
        } else {
          this.count -= 1;

        }
      },

      //提交订单事件
      orderSubmit:function () {
        event.preventDefault();
        var data = new FormData();

        data.append('orderMessage', this.orderMessage);
        data.append('orderTotalPrice', this.goodsDetail.goodsCurrnetScore*this.count);
        data.append('orderAddressId', this.orderAddress.address.addressId);
        data.append('orderUserId', this.orderUserId);

        var config ={
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }
        //提交订单
        axios
          .post('http://xiwanji.91xiaokong.com/orderInsert',data,config)
          .then(res=>{
            if(res.data.status == 200){
              alert(JSON.stringify(res.data.message));

            }else {
              alert(JSON.stringify(res.data.message));
            }

          })

      }


    },

  }
</script>

<style scoped>

  .aui-grid [class*=aui-col-] {padding: 0.75rem 0;}
  .z-background-image{background-image:none!important;}
  .aui-radio:checked{background:#fd5a41;border: 1px solid #fd5a41; }
  .z-order-font-color{color: orangered;font-size: 14px;}
  .z-order-margin{margin-left: 20px;display: inline-block;margin-top: 15px;}

</style>



