<script>

  import axios from 'axios'

  export default {
    name: "center",
    data(){
      return{
        //添加图片背景
        note: {
          backgroundImage: "url(" + require("../../images/mcbg.png") + ")",
          backgroundRepeat: "no-repeat",
          backgroundSize: "cover",
          height: "160px",
          position: "relative"
        },

        userId:'1',

        lists:{},
      }
    },
    created:function(){
      this.score();
    },
    methods:{
      // 提现事件
      score:function () {
        //初始化加载的数据
        var data = new FormData();
        data.append('userId', this.userId);
        axios
          // .post('http://140.143.200.89:8080/dishwasher/selectByScore',data)
          .post('http://xiwanji.91xiaokong.com//selectByScore',data)
          .then(response =>{
            this.lists = response.data
            //console.log(JSON.stringify(this.lists));

          })
      }

    },
  }
</script>

<style scoped>


</style>



