<template>
    <div class="adressList">
      <!--头部-->
      <header class="aui-bar aui-bar-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12">地址列表</div>
        <a class="aui-btn aui-pull-right">
          <router-link to="addAdress">

            <span class="aui-iconfont aui-icon-plus"></span>
          </router-link>
        </a>
      </header>
      <!--内容区-->
      <ul class="aui-list aui-media-list" style="background: #f2f2f2;margin-bottom: 100px;">
        <template v-for="items in adressList">
          <li class="aui-list-item  z-margin-b20" style="background: white">
            <div class="aui-media-list-item-inner">
              <div class="aui-list-item-inner">
                <div class="aui-list-item-title">
                  <span class="aui-pull-left aui-font-size-14">{{items.addressName}}</span>
                  <span class="aui-pull-right aui-font-size-14 z-color_hei">{{items.addressPhone}}</span>
                </div>

              </div>
            </div>
            <div class="aui-info z-edit-padding">
              <div class="aui-info-item aui-font-size-12">
                <span class="" style="color: black">{{items.addressAddress}}</span>

              </div>

            </div>

            <div class="aui-grid  z-padding-b-5 z-addressList-bg">
              <div class="aui-row">

                <div class="aui-col-xs-4">

                  <div class="aui-grid-label aui-font-size-12 aui-border-r z-colorshenhong"  v-if="items.addressDeleteState ==0">
                    <span class="iconfont">&#xebbb;</span>设为默认
                  </div>
                  <div class="aui-grid-label aui-font-size-12 aui-border-r"  v-else="items.addressDeleteState ==1">
                    <span class="iconfont">&#xebbb;</span>设为默认
                  </div>

                </div>
                <div class="aui-col-xs-4" v-if="items.addressDeleteState ==1">

                  <div class="aui-grid-label aui-font-size-12 aui-border-r">
                    <span class="iconfont">&#xebbb;</span>设为默认
                  </div>

                </div>
                <div class="aui-col-xs-4">

                  <!--因为没有addressId中是没有给出接口，直接把整个对象传到下一界面中-->
                  <router-link :to="{path:'editAdress',query:{addressInfo:items}}">
                    <div class="aui-grid-label aui-font-size-12 aui-border-r">
                      <span class="iconfont">&#xe6b8;</span>修改
                    </div>
                  </router-link>


                </div>
                <div class="aui-col-xs-4">
                  <div class="aui-grid-label aui-font-size-12" @click="deteFn(items.addressId)">
                    <span class="iconfont">&#xe605;</span> 删除
                  </div>
                </div>

              </div>
            </div>

          </li>

        </template>



      </ul>

    </div>
</template>

<script>
  import axios from 'axios';
    export default {
          name: "adressList",
          data(){
              return {
                userId: '1',
                adressList: {},
                addressId: ''
              }
        },
      created: function(){
        //console.log(JSON.stringify(this.datas));
        this.selectAllToAddress();
      },
      methods:{
        //  展示所有地址列表
        selectAllToAddress:function () {
          //初始化加载的数据
          var data = new FormData();
          //console.log(this);
          data.append('userId', this.userId);
          axios
            .post('http://xiwanji.91xiaokong.com/selectAllToAddress', data)
            .then(response => {

              this.adressList = response.data;
               //console.log(JSON.stringify(this.adressList));
            })
            .catch(error => {
              console.log(error)
            })
        },
        //删除地址项
        deteFn:function (event) {

          //传入的地址id
          // console.log(event);
          var data = new FormData();
          //event主要是接收由点击删除时的对应的 addressId
          data.append('addressId', event);

          axios
            .post('http://xiwanji.91xiaokong.com/deleteToAddress?addressId=',data)
            .then(res=>{

              if (res.data.status == 200){
                alert(JSON.stringify(res.data.message));
                this.adressList.splice(index, 1)
                this.selectAllToAddress();

              }else {
                alert(JSON.stringify(res.data.message));
              }
            })
        }
      },
      mounted: function(){


      },
    }
</script>

<style scoped>
  .aui-grid [class*=aui-col-]{padding: 0!important;}
  .z-addressList-bg{border-top: 1px solid #dddddd;width: 97%;}
  .z-edit-padding{padding-top: 0px;line-height: 40px;}

</style>



