<template>
    <div class="addshoukuangfashiI">
      <!--头部-->
      <header class="aui-bar aui-bar-nav" id="aui-header">
        <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
          <span class="aui-iconfont aui-icon-left"></span>
        </a>
        <div class="aui-title aui-font-size-12">添加收款账户</div>
      </header>

      <!--内容区-->

      <ul class="aui-list aui-form-list">
        <li class="aui-list-item">

          <div class="aui-list-item-inner">
            <!--<div class="aui-list-item-title aui-font-size-12">收款方式</div>-->
            <input type="text" class="aui-font-size-12" name="accountType" placeholder="收款方式" v-model="accountType">

          </div>
        </li>

        <li class="aui-list-item">
          <div class="aui-list-item-inner">
            <div class="aui-list-item-input">
              <input type="text" class="aui-font-size-12" name="accountAccountNumber" placeholder="收款账号" v-model="accountAccountNumber">
            </div>
          </div>
        </li>
        <li class="aui-list-item">
          <div class="aui-list-item-inner">

            <div class="aui-list-item-input">
              <input class="aui-font-size-12" type="text" name="accountNameReal" placeholder="真实姓名" v-model="accountNameReal">
            </div>
          </div>
        </li>

        <li class="aui-list-item">
          <div class="aui-list-item-inner">

            <div class="aui-list-item-input">
              <input class="aui-font-size-12" type="number" name="accountMobile" placeholder="手机号" v-model="accountMobile">
            </div>
          </div>
        </li>


      </ul>


      <!--保存-->
      <div class="aui-btn aui-btn-info aui-btn-block aui-btn-sm z-margin-t100 btn-bg" @click="accountInsert($event)">保存</div>

    </div>
</template>

<script>
    import axios from 'axios'


    export default {
        name: "addshoukuangfashiI",
        data(){
            return{
              accountType:'',
              accountAccountNumber:'',
              accountMobile:'',
              accountNameReal:'',
              accountUserId:'1',

            }
        },
        created:function(){
        },
        methods:{
            //提交保存按钮
                accountInsert:function (event) {
                  event.preventDefault();
                  var data = new FormData();
                  //账户类型
                  data.append('accountType', this.accountType);
                  //账号-
                  data.append('accountAccountNumber', this.accountAccountNumber);
                  //手机号
                  data.append('accountMobile', this.accountMobile);
                  //账号的真实姓名
                  data.append('accountNameReal', this.accountNameReal);
                  //用户id
                  data.append('accountUserId', this.accountUserId);

                  var config ={
                    headers: {
                      'Content-Type': 'multipart/form-data'
                    }
                  }
                  var reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
                  //逻辑处理

                    if (this.accountType == '') {
                      alert('请填写账号类型');
                      return;
                    } else if (this.accountAccountNumber == '') {
                      alert("请输入账号");
                      return;
                    } else if (this.accountNameReal == '') {
                      alert("请输入真实姓名");
                      return;

                    } else if (this.accountMobile == '') {
                      alert("请输入手机号码");
                      return;
                    } else if (!reg.test(this.accountMobile)) {
                      alert("手机格式不正确");
                      return;
                    }

                  axios
                    .post('http://xiwanji.91xiaokong.com//accountInsert',data,config)
                    .then(function(res){
                      // console.log(JSON.stringify(res));
                      if(res.data.status == 200){
                        alert(JSON.stringify(res.data.message));

                      }else {
                        alert(JSON.stringify(res.data.message));
                      }
                    })

                }

        },
        mounted(){



        },

      components:{
        // mtactionsheet
      },






    }
</script>

<style scoped>

</style>



