<template>
  <div class="xufei bg-content">
    <!--头部-->
    <header class="aui-bar aui-bar-nav" id="aui-header">
      <a class="aui-btn aui-pull-left" tapmode="" onclick="history.go(-1)">
        <span class="aui-iconfont aui-icon-left"></span>
      </a>
      <div class="aui-title aui-font-size-12">设备续费</div>
    </header>


    <!--选卡区域-->
    <div class="aui-card-list-header">
      <span class="aui-font-size-14 colorOrage">温馨提示: 1元=100积分</span>
    </div>
    <!--第一种方式-->
    <div class="aui-card-list-content-padded z-padding-b10">
    <template v-for="(item,index) in vipInfo">



      <div class="z-card-box" v-if="">
        <img class="z-card-img" src="../../images/yuecar.png">
        <div class="z-card-tit">
          <p class="z-card-tit-p1">
            <span style="">续费</span>
            <span class="z-xufei-xian"></span>
            <span class="z-padding-l15">{{item.vipType}}</span>
          </p>
          <p class="z-card-tit-p2">{{item.vipScore}}元</p>
          <p class="z-card-tit-p3">即缴费起{{item.vipTerm}}天</p>
        </div>
        <div class="tabName">
          <!---->
          <span class="z-card-span z-colorhuang" @click="clickVip(item)">请选择</span>
        </div>
      </div>

    </template>

    </div>

    <!--底部-->
    <footer class="aui-bar aui-bar-tab aui-margin-t-15">
      <div class="aui-bar-tab-item aui-font-size-14 colorBlack aui-text-left z-padding-l2 z-card-score"
           style="width: auto;">
       {{vipItem.vipType}}：{{vipItem.vipScore}}元
      </div>
      <div class="aui-bar-tab-item  z-backgroundshenju"  style="width: 5.5rem;" @click="duihuan($event)">
        <div class="aui-bar-tab-label">积分兑换</div>
      </div>
      <div class="aui-bar-tab-item z-backgroundyellow" tapmode="" style="width:5.5rem;">

        <div class="aui-bar-tab-label">现金购买</div>
      </div>

    </footer>


  </div>
</template>

<script>
  import axios from 'axios';

  export default {
    name: "xufei",
    data() {
      return {
        vipId: '',
        vipType: '',
        vipScore: '',
        vipTerm: '',


        vipInfo:{},
        vipItem:{},
        userId:'',
        deviceId:'',


      }
    },
    created: function () {
      //调用初次加载数据的方式；
      this.vipFee();
    },
    methods: {
      //展示不同的卡片内容
      vipFee: function () {

        //初始化加载的数据
        var data = new FormData();
        data.append('vipId', this.vipId);
        data.append('vipType', this.vipType);
        data.append('vipScore', this.vipScore);
        data.append('vipTerm', this.vipTerm);
        //发送接口请求
        axios
          // .post('http://140.143.200.89:8080/dishwasher/vipSelectAll', data)
          .post('http://xiwanji.91xiaokong.com/vipSelectAll', data)
          .then(res => {

           this.vipInfo = res.data;

          })

      },
      //  点击选择的事件
      clickVip:function (item) {
        console.log(JSON.stringify(item));

        this.vipItem = item;
      },

    //  点击积分兑换事件
      duihuan:function (event) {
        event.preventDefault();
        var data = new FormData();
        //注释：需要把上一界面传过来的值赋值到点击更改相应对象中
        data.append('addressName', this.addressInfo.addressName);

      }



    },

  }
</script>

<style scoped>

</style>



